/* eslint-disable import/no-anonymous-default-export */
import React from "react";
import "./Card.css";
import Button from "../Navbar/Button";

export default (props) => (
	<div className="Card">
		<div className="Image">
			<img className="foto" src={props.src} />
		</div>

		<div className="Descricao">{props.titulo}</div>
		<div className="Valor">
			<b>R${props.valor}</b>
		</div>
		<div className="Botao">
			<Button>Presentear</Button>
			{/* <a href="">CLICK</a> */}
		</div>
	</div>
);
