/* eslint-disable import/no-anonymous-default-export */
import React from "react";
import "../css/Nome.css";

export default (props) => {
	return (
		<div className="nome">
			<div className="bgtema"></div>

			<div className="texto">
				<h1>Significado do Nome Murillo</h1>
				<h2>
					&emsp; Murillo: Significa "pequeno muro", “pequena parede” ou "pequena
					muralha".
				</h2>
				<h3>
					&emsp; Trata-se de um nome bastante popular no Brasil, que reflete o
					significado da força, da defesa, da proteção.
					<p />
					&emsp; Desta forma, os meninos registrados com esse nome possivelmente
					carregarão consigo essas mesmas qualidades, fazendo com que as pessoas
					próximas se sintam protegidas pela sua presença.
					<p />
					&emsp; No Brasil, o apelido mais comum para esse bonito nome masculino
					é, mais uma vez, o diminutivo “Murilinho”.
					<p />
					&emsp; Tem origem no nome espanhol Murillo, cujo étimo é a palavra do
					latim múrus, que quer dizer "parede" ou "muralha".
					<p />
					&emsp; Murillo é o diminutivo das palavras, em espanhol, muro (parede,
					muro) ou muralla (muralha), de modo que Murillo significa "pequeno
					muro", “pequena parede” ou "pequena muralha".
					<p />
					&emsp; Inicialmente era utilizado como apelido, assumindo mais tarde a
					posição de primeiro nome.
					<p />
					&emsp; Assim, acredita-se que tenha sido usado para designar um homem
					baixo e forte, tal como as características atribuídas aos muros.
					<p />
					&emsp; Na Espanha, Bartolomé Esteban Murillo (1617-1682) é considerado
					o melhor pintor do barroco espanhol.
					<p />
					&emsp; No Brasil, José Murillo de Carvalho é um historiador. Em 2004
					se tornou membro da Academia Brasileira de Letras.
					<p />
					&emsp; Ainda no nosso país, têm destaque no campo artístico os atores
					Murillo Benício, Murillo Rosa e Murillo Couto.
					<p />
				</h3>
			</div>
		</div>
	);
};
